<template>
    <div id="noteDetail">
        <h1>{{ msg }} : {{ $route.params.noteId }}</h1>
    </div>
</template>

<script>
export default {
    data() {
        return {
            msg: '笔记详情页'
        }
    }
}
</script>

<style scoped>
h1 {
    color: rgb(103, 169, 230);
}
</style>


